export default {
	name: 'refresh',
	props: {
		  isRequest: {
			type: Function,
			default: null
		  },
		  cid: String
	},
	data() {
		return {
			isTranf: 0,
			touchStart: '',
			touchMove: '',
			isEnd: 0
		};
	},
	methods: {
		refreshStart(e) {
			if (this.isEnd == 0) {
				this.touchStart = e.changedTouches[0].pageY
			}
		},
		refreshMove(e){
			if (this.isEnd == 0) {
				var touchStart = this.touchStart,
					oldMove = this.touchMove,
					newMove = e.changedTouches[0].pageY
				if (touchStart <= newMove) {
					var isTranf = touchStart > newMove ? 0 : newMove - touchStart
					this.isTranf = isTranf
					this.touchMove = e.changedTouches[0].pageY
				}
			}
		},
		refreshEnd(e) {
			if (this.isEnd == 0) {
				if (this.isTranf >= 90) {
					this.isTranf = 100
					this.isEnd = 1
					this.isRequest().then((res) => {
						this.isEnd = 2
						setTimeout(() => {
							this.isTranf = 0
							this.isEnd = 0
							this.$parent.refresh();
						}, 600)
					})
				} else {
					this.isTranf = 0
				}
			}
		},
		endAfter(){
			this.isEnd = 2
			setTimeout(() => {
				this.isTranf = 0
				this.isEnd = 0
			}, 600)
		}
	},
	computed: {
		isTranform() {
			var isTranf = this.isTranf > 150 ? 150 : this.isTranf
			return `transform: translateY(${isTranf-100}px);`
		},
		isRotate() {
			var rotate = 360 / 100 * this.isTranf
			return `transform: rotate(${rotate}deg);`
		}
	}

}
